import React from 'react';
import axios from 'axios';
import Movie from './Movie'
//내가쓸 api주소https://yts-proxy.now.sh/list_movies.json";


class App extends React.Component{
    
    state={
        isLoading: true ,
        movies:[]
    }
    //1----component가 mount되자 마자 호출되어있다
    //data를 fatch가 완료되면 동작을 실행한다
    // axios가 항상빠르지 않기떄문에 함수가 끝날떄까지 기다려달라고 전해야한다
    getMovies = async() =>{
        const {
            data:{
                data:
                {movies}
            }
        }=await axios.get("https://yts-proxy.now.sh/list_movies.json?sort_by=rating");
       
         
        this.setState({movies:movies,isLoading:false});
  
        console.log(this.state.movies);
    }
    componentDidMount(){
      this.getMovies();
         
   
    }
    //선택지 1,<div>{isLoading ? "Loading": "movies.map"}   </div>
    //2.함수를 만들어서 넣던가 해라
    render()
    {
        const {isLoading,movies}=this.state;
        return (
            <section class="container">
                {isLoading ? (<div class="loader">
                <span class="loader__text">
               "Loading..."
               </span>

               </div>)
               :(
               <div class="movies">
                {movies.map(movies =>(
                 <Movie
                key={movies.id} 
                 id={movies.id} 
                 rating={movies.rating}
                 year={movies.year} 
                 title={movies.title}
                 summary={movies.summary} 
                 poster={movies.medium_cover_image} 
                 genres={movies.genres}/>
                
                
                )
                )
                }
                    </div>)
    }
            </section>
        )


      
    }
}
  
  export default App;
  