//목적 ----movie를 render 해줄것이다

import React from "react";
import ProTypes from "prop-types";
//state가 필요없다 why-----추후 생각해 볼수있도록 하자

function Movie({id,year,title,summary,poster,rating,genres})
{
    return (
    <div class="movie__data">
        <img src={poster} ></img>
        <h3 class="movie__title">{title}</h3>
    <h3 class="movie__rating">{rating} </h3>
                <h5 class="movie__year">{year}</h5>
    <p class="movie_summary">{summary}</p>
    <ul className="geners">
        {genres.map((genre,number) =>
        
        <li className="genres__genre" key={number}>{number}{genre} </li>
        )}
    </ul>

    </div>
    );
}
Movie.ProTypes={
    
    id: ProTypes.number.isRequired,
    year: ProTypes.number.isRequired,
    title:  ProTypes.string.isRequired,
    summary:  ProTypes.string.isRequired,
    poster: ProTypes.string.isRequired,
    genres: ProTypes.arrayOf(ProTypes.string).isRequired
}

export default Movie;
